*,
*:before,
*:after {
    box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    padding: 0;
}

p {
    margin: 0;
    padding: 0;
}

body {
    font-family: "subset-Graphik-Regular";
    color: #0a0a0a;
    -webkit-font-smoothing: antialiased;
    margin: 0;
    padding: 0;
}


/*Container*/

.container {
    padding-left: 15px;
    padding-right: 15px;
    width: 100%;
    max-width: 1180px;
    margin: 0 auto;
}


/*/Container*/


/*Header*/

.header:after {
    margin-top: 10px;
    opacity: .5;
    content: "";
    border: 1px solid #e8e8e8;
    display: block;
}

.attetion__footer-text-2,
.attetion__footer-text-3 {
    padding: 0 !important;
}

.header__inner {
    align-items: center;
    padding-top: 15px;
    display: flex;
    justify-content: space-between;
}

.header__search {
    cursor: pointer;
}

.nav__link {
    text-decoration: none;
    color: #0a0a0a;
    font-weight: 400;
    font-size: 16px;
    padding-right: 30px;
    transition: color .2s linear;
}

.nav {
    display: flex;
    align-items: center;
}

.nav__link:hover {
    color: #01be66;
    transition: color .2s linear;
}

.nav__link--btn {
    padding-right: 0;
    color: #01be66;
    transition: color .2s linear;
}

.nav__link--btn:hover {
    color: #0a0a0a;
    transition: color .2s linear;
}

.nav__img {
    padding-right: 7px;
}

.linked {
    padding-top: 3px;
    display: flex;
    align-items: center;
}


/*/Header*/

.rev__inner {
    display: flex;
    justify-content: space-between;
}

.rev__inner-right {
    display: flex;
}

.rev__inner-reviews {
    position: relative;
    margin-left: 25px;
}

.rev__inner-reviews:first-child {
    margin-left: 0;
}

.rev__inner-right {
    position: relative;
}

.rev__inner-right:before {
    content: "";
    width: 100%;
    height: 2px;
    border: 2px solid #f3f3f3;
    position: absolute;
    top: 25px;
    left: 0;
    z-index: 1;
}

.rev__inner-reviews:hover::before {
    display: block;
}

.rev__inner-reviews.new:before {
    display: block;
}

.rev__rev {
    color: #0a0a0a;
    font-size: 36px;
    font-family: 'subset-GraphikLCG-Bold';
    font-weight: 700;
}

.rev__num {
    color: #959595;
}

.new {
    color: #01be66;
}

.rev__inner-reviews:hover {
    color: #01be66;
    cursor: pointer;
}

.rev__inner {
    position: relative;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
}

.rev__inner-reviews:before {
    content: "";
    width: 100%;
    height: 2px;
    border: 2px solid #01be66;
    position: absolute;
    top: 25px;
    left: 0;
    display: none;
    z-index: 2;
}


/* DIALOG*/

.dialog {
    padding-bottom: 40px;
}

.dialog__inner {
    display: flex;
}


/*LEFT*/

.dialog__inner-left {
    margin-top: 50px;
    width: 78%;
    padding-right: 10px;
}

.attention__inner {
    display: flex;
    margin-bottom: 15px;
    padding-top: 15px;
    padding-bottom: 10px;
    padding-left: 15px;
    border: 1px solid #e8e8e8;
}

.attention__left img {
    padding-right: 15px;
}

.attetion__span {
    font-family: 'subset-Graphik-Semibold';
    font-weight: 600;
}

.attention__footer-inner {
    margin-top: 15px;
    display: flex;
    align-items: center;
}

.attention__footer-right img {
    padding-right: 10px;
}

.attention__footer-text-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.attetion-green {
    color: #01be66;
    font-size: 20px;
    font-family: 'subset-Graphik-Semibold';
    font-weight: 600;
}

.attetion__footer-text-1 {
    line-height: 1.3;
    color: #0a0a0a;
    font-size: 16px;
    font-family: 'subset-Graphik-Semibold';
    font-weight: 600;
    padding-right: 80px;
}

.attetion__span-footer {
    color: #0a0a0a;
    font-family: 'subset-Graphik-Semibold';
    font-weight: 600;
    font-size: 16px;
}

.attetion__footer-text-2 {
    padding-left: 155px;
}

.attetion__footer-text-3 {
    padding-left: 205px;
}

.attention__footer-inner .attetion__footer-text-4 {
    position: relative;
    color: #0a0a0a;
    font-size: 16px;
    width: 58%;
    font-weight: 600;
    font-family: 'subset-Graphik-Semibold';
}

.personal--grey {
    color: #959595;
    font-weight: 400;
    font-size: 13px;
}

.only--rev {
    width: 60%;
}

.personal__rev__inner {
    display: flex;
    justify-content: space-between;
}

.center__rev-btn {
    padding-left: 38px;
    padding-top: 20px;
}

.peronal__rev__btn {
    padding: 20px 25px;
    max-height: 60px;
    text-decoration: none;
    color: #0a0a0a;
    border: 1px solid #01be66;
    display: block;
    transition: color, background-color .2s linear;
}

.peronal__rev__btn:hover {
    background-color: #01be66;
    color: #fff;
    transition: color, background-color .2s linear;
}

.attention__footer-btn {
    border-radius: 3px;
    display: inline-block;
    text-decoration: none;
    color: #fff;
    background-color: #01be66;
    padding: 20px 25px;
    margin-left: 100px;
    transition: background-color .2s linear;
}

.attention__footer-btn:hover {
    background-color: #488d6d;
    transition: background-color .2s linear;
}

.attention__orange {
    color: #f98e3c;
    font-size: 20px;
    font-weight: 600;
    font-family: 'subset-Graphik-Semibold';
}

.perosnal__left-rev {
    display: flex;
    align-items: center;
}

.personal__name {
    display: block;
    margin-left: 15px;
    color: #0a0a0a;
    font-size: 16px;
    font-weight: 400;
}

.personal__hotel {
    display: block;
    padding-top: 5px;
    color: #0077cc;
    font-size: 16px;
    font-family: 'subset-Graphik-Semibold';
    font-weight: 600;
}

.personal__hotels {
    display: block;
    padding-top: 5px;
    color: #01be66;
    font-size: 16px;
    font-family: 'subset-Graphik-Semibold';
    font-weight: 600;
}

.personal__rev-only {
    padding-top: 15px;
    line-height: 1.6;
    color: #0a0a0a;
    font-weight: 400;
    font-size: 15px;
}


/*/LEFT*/


