@font-face {
  font-family: 'icomoon';
  src:  url('../icons/icomoon.eot?3e7wos');
  src:  url('../icons/icomoon.eot?3e7wos#iefix') format('embedded-opentype'),
    url('../icons/icomoon.ttf?3e7wos') format('truetype'),
    url('../icons/icomoon.woff?3e7wos') format('woff'),
    url('../icons/icomoon.svg?3e7wos#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-checkmark:before {
  content: "\e902";
}
.icon-cheveron-down:before {
  content: "\e900";
}
.icon-cheveron-right:before {
  content: "\e901";
}
